import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import cssutil from '@/components/cssutil'
import jsutil from '@/components/jsutil'
import pyutil from '@/components/pyutil'
import archive from '@/components/archive'

Vue.use(Router)

export default new Router({
  //mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/css',
      name: 'css',
      component: cssutil
    },
    {
      path: '/js',
      name: 'js',
      component: jsutil
    },
    {
      path: '/python',
      name: 'python',
      component: pyutil
    },
    {
      path: '/archive/:id',
      name: 'archive',
      component: archive
    }
  ]
})



