// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import app from './app';
import router from './router';

import './assets/style/reset.css';
import './assets/style/common.css';
import './assets/font/fontello/css/fontello.css';
import './assets/style/tag.css'
import './assets/style/style.css';

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
    el: '#app',
    data: {
        color: ['green', 'blue', 'black', 'red'],
        articles: [{
            title: 'git常用命令',
            text: '用gitbash进入类似命令行的窗口,用命令 cd e:/learngit 进入该目录，然后在此目录下初始化$ git init, 于是该文件夹就成为了一个工作区，里面的.git文件就是版本库（repository）,在工作区中新建一个文件，比如XXX.txt文件,在txt中添加‘1111’文本，用 $ git add XXX.txt命令将XXX.txt添加到缓存中',
            tags: ['git'],
            id: 0
        },{
            title: '浏览器页面解析过程',
            text: '浏览器的渲染引擎（如 chrome 的webkit）会先构建DOM树（从<html>标签开始），在遇到外部css文件时会停止构建DOM树，转而解析css文件，构建CSSOM树，完成后会继续构建DOM树，同时与已经构建好的DOM树节点共同构建渲染树，并将内容绘制在屏幕上，而这是一个渐进的过程',
            tags: ['css','browser'],
            id: 1
        },{
            title: 'Angularjs directive',
            text: 'Angularjs里的指令定义很常用的也是很重要的,priority: 在同一个元素上的指令，数字越大优先级越高，优先级越高controller和pre-link函数越先执行，而post-link则相反。该项不适用不在同一元素上的指令,terminal：如果设置为true，那么该指令最后一个执行，优先级低于该指令的指令全都被忽略',
            tags: ['angular','js'],
            id: 2
        },{
            title: 'Angularjs 服务注册',
            text: '理解Angularjs服务注册过程，$injector用来提供正确的服务provider，实例化服务，然后调用他的$get方法来获取服务实例，$provide 服务有一系列的方法（6个方法: provider,value,constant,factory,service,decorator)，这些方法用 $injector来注册组件，大部分的方法都暴露在了angular.module下，也就是可以直接在module下调用这些函数。',
            tags: ['angular','js'],
            id: 3
        },/*{
            title: 'Angularjs scope',
            text: '理解Angularjs里的作用域，stopPropagation只针对$emit, 如果使用$scope.$broadcast(‘you’) 给you事件发送广播,那么event对象里就不会有stopPropagation方法，即使在子scope再使用$scope.$emit(‘you’)发送消息，调用该方法仍然报错。',
            tags: ['angular','js'],
            id: 4
        },*/]
    },
    router,
    template: '<app/>',
    components: { app }
})



