<template>
  <div>
    <article class="article-item" v-for="article in articles">
      <h3 class="article-title">
        <a :href="createLink(article.id)" class="article-title-link">
          {{article.title}}
        </a>
      </h3>
      <div class="article-profile">
        <span class='article-text'>
          {{article.text}}
        </span>
        <span>......</span>
        <a href="#/archive/1" class="article-readall">阅读全文</a>
      </div>
      <div class="article-footer">
        <a class="tag" v-for="(tag ,index) in article.tags" :class="color[index]">
        {{tag}}
        </a>
      </div>
    </article>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      color: this.$root.color,
      articles: this.$root.articles
    }
  },
  methods: { 
    createLink(id){
      return '/archive/'+id
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .article-item{
    position: relative;
    width: 840px;
    padding: 16px 20px 0;
    margin-bottom: 30px;
    background: #fafafa;
    border-radius: 3px;
    box-shadow: 1px 2px 2px rgba(123, 122, 122, 0.58);
  }
  .article-item:after{
    content: '';
    position: absolute;
    right: -2px;
    top: 16px;
    width: 10px;
    height: 20px;
    border-radius: 10px 0 0 10px;
    background-color: #d6f4ff;
    box-shadow: 1px 1px 2px rgba(123, 122, 122, 0.9) inset;
  }
  .article-title,
  .article-profile{
    margin-bottom: 15px;
    font-size: 14px;
  }
  .article-title-link{
    position: relative;
    display: inline-block;
    font-size: 20px;
    text-decoration: none;
    color: #009fb3;
  }
  .article-title-link:hover{
    color: #00b5ad;
  }
  .article-title-link::after{
    content: "";
    display: block;
    height: 2px;
    background-color: #00b5ad;
    transform: scaleX(0);
    transition: .3s ease-in-out;
  }
  .article-title-link:hover::after{
    transform: scaleX(1);
  }
  .article-text{
    color: #333;
  }
  .article-readall{
    color: #009fb3;
    text-decoration: none;
  }
  .article-readall:hover{
    color: #017b8a;
    text-decoration: underline;
  }
  .article-footer{
    margin: 0 -20px;
    padding-left: 20px;
    line-height: 40px;
    border-top: 1px solid #ccc;
  }
</style>


